const $ = require('jquery');
const _ = require('underscore');



class Humburger{
	constructor(className, targetToggle, addedClass){
		this.className = document.getElementByClassName(`${className}`);
		this.targetToggle = document.getElementByClassName(`${className}`);
		this.addedClass =  addedClass;
	}
	go(){
		this.className.addEventListener('click', ()=>{
			if(!this.className.classList.contains('is-active')){
				this.targetToggle.classList.add(`${this.addedClass}`);
			}else{
				this.targetToggle.classList.remove(`${this.addedClass}`);	
			}
		});
	}

}

module.exports = Humburger;